import React, { useContext, useState, Fragment } from 'react';
import { CountryStatsContext } from './CountryStatsProvider';
import Pagination from './Pagination';
import { Table, Form } from 'react-bootstrap';

export function WorldTable() {
    const [data] = useContext(CountryStatsContext);
    const [numPerPage] = useState(5);
    const totalNumPages = Math.ceil(data.length / numPerPage);

    const [filteredData, setFilteredData] = useState([...data]);
    const [selectedData, setSelectedData] = useState(filteredData.slice(0, 5));
    const [numPages, setNumPages] = useState(totalNumPages);
    const [currentPage, setCurrentPage] = useState(1);

    const handleClick = (selectedPage) => {
        let selected = filteredData.slice((selectedPage * numPerPage) - numPerPage, selectedPage * numPerPage)
        setSelectedData(selected);
        setNumPages(Math.ceil(filteredData.length / numPerPage));
        setCurrentPage(selectedPage);
    }

    const handleCountryChange = (e) => {
        let entry = e.target.value;
        let filtered = data.filter(cnt => cnt.country.toLowerCase().includes(entry.toLowerCase()));
        setFilteredData(filtered);
        setSelectedData(filtered.slice(0, numPerPage));
        setNumPages(Math.ceil(filtered.length / numPerPage));
        setCurrentPage(1);
    }

    return (
        <Fragment>
            <Form>
                <h3>Cases by Country</h3>
                <Form.Row className='p-3'>
                    <Form.Control type='text' placeholder='Filter country' onChange={handleCountryChange} />
                </Form.Row>
            </Form>
            <Table striped responsive>
                <thead>
                    <tr>
                        <th>Country</th>
                        <th>Cases</th>
                        <th>Deaths</th>
                        <th>Recovered</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        selectedData.map((cnt, index) => 
                            <tr key={cnt.country + index}>
                                <td>{cnt.country}</td>
                                <td>{cnt.cases ? cnt.cases.toLocaleString(undefined, { maximumFractionDigits: 0 }) : 'No data.'}</td>
                                <td>{cnt.deaths ? cnt.deaths.toLocaleString(undefined, { maximumFractionDigits: 0 }): 'No data.'}</td>
                                <td>{cnt.recovered ? cnt.recovered.toLocaleString(undefined, { maximumFractionDigits: 0 }): 'No data.'}</td>
                            </tr>
                        )
                    }
                </tbody>
            </Table>
            <Pagination handleClick={handleClick} numOfPages={numPages} currentPage={currentPage} />
        </Fragment>
    );
}