import React, { useState, createContext, useEffect } from 'react';
import { Spinner, Row } from 'react-bootstrap';

export const CountryStatsContext = createContext();

export function CountryStatsProvider(props) {

    const [countryStats, setCountryStats] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        getCountryStats();
    }, []);

    const getCountryStats = async () =>  {
        setIsLoading(true);
        const res = await fetch("https://coronavirus-19-api.herokuapp.com/countries");
        const data = await res.json();
        setCountryStats(data);      
        setIsLoading(false);
    } 

    return(
        <CountryStatsContext.Provider value={[countryStats,setCountryStats]}>
            {isLoading ? <Row style={{display: 'flex', justifyContent: 'center', alignContent: 'center', padding: '1em'}}><Spinner animation="grow" /> </Row> : props.children}
        </CountryStatsContext.Provider>
    );
}