import React, { useState, useContext} from 'react';
import { Row, Col } from 'react-bootstrap';
import StatCard from './StatCard.js';
import Axios from 'axios';
import { CountryStatsContext } from './CountryStatsProvider.js';

function PhilStats() {

    const [data] = useContext(CountryStatsContext);
    
    let phil = data.find(d => d.country.includes('Philippines'));
    let rc = phil ? `${((phil.recovered / phil.cases) * 100).toLocaleString(undefined, { maximumFractionDigits: 2})} % recovered` : 'No data available.'
    let dc = phil ? `${((phil.deaths / phil.cases) * 100).toLocaleString(undefined, { maximumFractionDigits: 2})} % died` : 'No data available.'
    return (
        <Row>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-procedures"></i>Cases</span>} main={<span>{phil? phil.cases : 'No data'}</span>} footer='Total of all cases.' style='bg-info mb-3 text-white' />
            </Col>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-user"></i>Recovered</span>} main={<span>{phil? phil.recovered : 'No data'}</span>} footer={rc} style='bg-success mb-3 text-white' />
            </Col>
            <Col sm={6} md={4}>
                <StatCard title={<span><i className="las la-skull"></i>Deaths</span>} main={<span>{phil? phil.deaths : 'No data'}</span>} footer={dc} style='bg-danger mb-3 text-white' />
            </Col>
        </Row>
    );
}

export default PhilStats;
