import React, { useState, useEffect } from "react";
import {Pagination as BootstrapPagination} from 'react-bootstrap';

export default function Pagination(props) {

    const [currentPage, setCurrentPage] = useState(props.currentPage);

    useEffect(() => {    
        setCurrentPage(props.currentPage);        
    },
    [props.currentPage]);

    const handleClick = (e, page) => {
        e.preventDefault();
        props.handleClick(page);
    };

    const range = (start, end, step = 1) => {
        let rangeArr = [];
        for (let index = start; index <= end; index = index + step) {
            rangeArr.push(index);
        }
        return rangeArr;
    };

    const DOTS = '...';

    const getPages = () => {
        // case 1 BOTH SPILLS
        //(1) ... {4} {5} [6] {7} {8} ... (10)

        // case 2 LEFT SPILL
        //(1) ... {5} {6} [7] {8} {9} (10)

        // case 2 RIGHT SPILL
        //(1) {2} {3} [4] {5} {6} ... (10)


        const neighbors = 1; // num of pages to the side of current
        const totalPages = props.numOfPages;
        const numControls = (neighbors * 2) + 3; // the neighbors, current page, first and last pages

        if (totalPages > numControls) {
            const neighborStart = Math.max(2, currentPage - neighbors);
            const neighborEnd = Math.min(currentPage + neighbors, props.numOfPages - 1);

            let pages = range(neighborStart, neighborEnd); // current and neighbors

            const hasLeftSpill = neighborStart > 2;
            const hasRightSpill = (totalPages - neighborEnd) > 1;
            const spillOffSet = numControls - (pages.length - 1);

            if (hasLeftSpill && hasRightSpill) {
                pages = [1, DOTS, ...pages, DOTS, totalPages];
            } else if (hasLeftSpill && !hasRightSpill) {
                const extrapages = range(neighborStart -spillOffSet, neighborStart - 1);
                pages = [1, DOTS, ...extrapages, ...pages, totalPages];
            } else {
                const extrapages = range(neighborEnd + 1, neighborEnd + spillOffSet);
                pages = [1, ...pages, ...extrapages, DOTS, totalPages];
            }
            return pages;
        }
        return range(1, props.numOfPages, 1);
    }

    let pageNums = getPages();
    return (
        <BootstrapPagination size='sm' className="uk-pagination">
            <BootstrapPagination.Prev onClick={(e) => {
                console.log("click");
                if (currentPage > 1) {
                    handleClick(e, currentPage - 1);
                }
            }}/>
            {
                pageNums.map((page, index) => {
                    if (page === DOTS) {
                    return <BootstrapPagination.Ellipsis key={index}/>
                    } else {
                        return <BootstrapPagination.Item className={currentPage === page ? "active" : null} onClick={(e) => handleClick(e, page)} key={index}>{page}</BootstrapPagination.Item>
                    }
                })
            }
            <BootstrapPagination.Next onClick={(e) => {
                console.log("click");
                if (currentPage < props.numOfPages) {
                    handleClick(e, currentPage + 1);
                }
            }}/>
        </BootstrapPagination>
    );
}