import React from 'react';
import { Card } from 'react-bootstrap';
import './index.css';

export default function StatCard(props) {
    return(
        <Card className={props.style}>
            <Card.Body>
                <Card.Title className='text-center font-weight-normal'>{props.title}</Card.Title>
                <Card.Text className='text-center font-weight-normal'>{props.main}</Card.Text>
                <Card.Subtitle className='text-center font-weight-normal'>{props.footer}</Card.Subtitle>
            </Card.Body>
        </Card>
    );
}