import React from 'react';
import { Badge } from 'react-bootstrap';

function Update({ title, body, date, priority,source }) {
    return (
            <div className='update-box mb-3'>
                <p className='update-title'>{title}</p>
                {priority === 1 && (<Badge variant="danger">Important</Badge>)}
                <p>{date}</p>
                <p className='update-text' dangerouslySetInnerHTML={{ __html: body }}></p>
                {source ? <a href={source}>Source</a> : null}
            </div>
    );
}

export default Update;
