import React, {useContext, Fragment} from 'react';
import { UpdateContext } from './UpdateProvider.js';
import Update from './Update.js';

function UpdateList() {

  const [updates] = useContext(UpdateContext);

  return (
    <Fragment>
      {updates.map(u => (
        <Update key={u.title} title={u.title} body={u.body} date={u.date} priority={u.priority} source={u.source}/>
      ))}
    </Fragment>  
  );
}

export default UpdateList;
