import React, { Fragment } from 'react';
import UpdateList from './UpdateList.js';
import PhilStats from './PhilStats';
import { UpdateProvider } from './UpdateProvider.js';
import { WorldTable } from './WorldTable.js';
import { CountryStatsProvider } from './CountryStatsProvider.js';
import { Container, Row, Col, Accordion, Card, Button } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';

function Home(props) {

  const history = useHistory();

  const routeTo = (where) => {
    history.push(where);
  }

  return (
    <Container>
      <Row>
        <Col>
          <p className='main-headline'>COVID-19 Situation in the Philippines</p>
        </Col>
      </Row>
      <CountryStatsProvider>
        <Row className='mb-3'>
          <Col>
            <PhilStats />
          </Col>
        </Row >
        <Row>
          <WorldTable />
        </Row>
      </CountryStatsProvider>
      <Row>
        <UpdateProvider>
          <Col sm={12} md={8}>
            <UpdateList />
          </Col>
        </UpdateProvider>
        <Col sm={12} md={4}>
          <Accordion defaultActiveKey="0">
            <Card>
              <Card.Header>
                <Accordion.Toggle as={Button} variant='link' eventKey='0'>What is COVID-19</Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey='0'>
                <Card.Body>According to the World Health Organization, COVID-19(or Coronavirus Disease 2019) is an infectious disease caused by the newly discovered SARS-CoV-2 virus. The most common
                symptoms are fever, dry cough and, shortness of breath. The elderly and those with preexisting conditions such as heart disease, diabetes, and hypertension are more likely
                to experience severe symptoms.
                COVID-19 is now a global pandemic.
                  </Card.Body>
              </Accordion.Collapse>
            </Card>
            <Card>
              <Card.Header>
                <Accordion.Toggle as={Button} variant='link' eventKey='1'>Prevention</Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey='1'>
                <Card.Body><ul>
                  <li>STAY AT HOME. Leave your house ONLY when necessary(like getting basic supplies such as food and medicine).</li>
                  <li>One of the most recommended ways of reducing the risk of being infected and/or spreading the disease is by frequent and thorough handwashing with soap and water.
                        Use alcohol-based sanitizer/disinfectant when soap and water are not available.</li>
                  <li>Disinfect frequently touched surfaces. Alcohol-based solutions are recommended.</li>
                  <li>Avoid touching your face, especially eyes, nose, or mouth</li>
                  <li>Avoid crowds, and practice social distancing. Maintain at least 3 feet distance from any other person, especially if showing signs of sickness.</li>
                  <li>If you believe you are experiencing symptoms related to COVID-19, seek medical attention and follow the orders of your local health authority.</li>
                </ul>
                </Card.Body>
              </Accordion.Collapse>
            </Card>
            <Card>
              <Card.Header>
                <Accordion.Toggle as={Button} variant='link' eventKey='2'>Contact Info</Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey='2'>
                <Card.Body>
                  <p>For Philippine residents, here are some contact numbers you may find helpful:</p>
                  <ul>
                    <li>Department of Health COVID Hotlines:
                                <ul>
                        <li>02-894-COVID (02-894-26843)</li>
                        <li>1555 (Smart and PLDT subscribers)</li>
                      </ul>
                    </li>
                    <li>Department of Health Telephone Nos.:
                            <ul>
                        <li>(632) 8651-7800 local 5003-5004 </li>
                        <li>(632) 165-364</li>
                      </ul>
                    </li>
                    <li>Research Institute for Tropical Medicine Telephone Nos.:
                                <ul>
                        <li>(+632)8807-2631</li>
                        <li>(+632)8807-2632</li>
                        <li>(+632)8807-2637</li>
                      </ul>
                    </li>

                  </ul>
                  <p>Hospitals are in dire need of medical supplies. The <a href='https://bit.ly/forCOVID19frontliners' target='_blank'>Kaya Natin! Movement</a> is open for donations.</p>
                  <p>Alternatively, if you have extra face masks, or other medical equipment ready to donate, the Philippine General Hospital is asking for donations. Tel: (02) 554-8400</p>
                  <p>Thank you to our medical heroes and other frontliners currently working non-stop to help the sick.</p>
                </Card.Body>
              </Accordion.Collapse>
            </Card>
          </Accordion>
        </Col>
      </Row>
    </Container>
  );
}

export default Home;
